.contenedorPrincipal {
    height: 947px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;


}

.clima-sol {
    z-index: -1;
    background-image: url("../imagenes/imagen-sunny.jpg");
    background-size: cover;
    /*cobre toda la ventana*/
    background-repeat: no-repeat;
    background-position: center;
    transition: all 1s ease-in-out;
}

