.contenedorInfo {
    width: 35%;
    background: rgb(2, 0, 36);
    background: linear-gradient(207deg, rgba(2, 0, 36, 0.4) 0%, rgba(9, 228, 232, 0.4) 40%, rgba(0, 212, 255, 0.4) 100%);
    border-radius: 5%;
    border: 3px solid white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    outline-color: white;
    box-shadow: 5px 5px #888888;
    min-width: 300px;
}
/* transparencia con rgba no afecta a los hijos */
.contenedorInfo:hover {
    background: linear-gradient(207deg, rgba(2, 0, 36, 0.7) 0%, rgba(9, 228, 232, 0.7) 40%, rgba(0, 212, 255, 0.7) 100%);

}