.contenedorInformacion {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: monospace, serif;
    color: black;
}

.ciudad {
    font-size: 20px;
}

.pais {
    font-weight: bold;
    font-size: 15px;

}

.clima {
    display: flex;
    flex-direction: row;
    align-items: center;

}

.icono {
    transform: scale(0.5);
}

.mapa-no {
    display: none;
}
.mapa-si {
    display: block;
    width: 100%;
    
}
iframe{
    margin: 15px;
    border-radius: 15px;
    border: 2px solid white;
}

.toggle{
    padding: 5px;
}
.switch {
    font-size: 17px;
    position: relative;
    display: inline-block;
    width: 3em;
    height: 1.5em;
}

/* Hide default HTML checkbox */
.switch .input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 30px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 1em;
    width: 1em;
    border-radius: 20px;
    left: 0.3em;
    bottom: 0.3em;
    background-color: white;
    transition: .4s;
}

.input:checked+.slider {
    background-color: #2196F3;
}

.input:focus+.slider {
    box-shadow: 0 0 1px #2196F3;
}

.input:checked+.slider:before {
    transform: translateX(1.5em);
}
